.mainContainer {
    position: absolute;
    top: 5svh;
    left: 50%;
    transform: translateX(-50%);

    width: 80vw;
    max-width: 300px;

    height: 50px;

    background-color: rgba(255, 255, 255, 1);
    box-shadow: var(--window-shadow);

    border-radius: 5px;

    transition: opacity .5s;

    transform-origin: left;
}

.scrollableContainer {
    position: relative;
    z-index: 10;

    overflow: hidden;
    width: 100%;
    height: 100%;
}

.contentContainer {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-columns: 100%;
    grid-auto-rows: 100%;
    align-items: center;
    justify-items: center;

    transition: transform .5s;
}

.contentContainer > div {
    position: relative;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    outline: solid black 1px;

    letter-spacing: -.05rem;
}

.contentContainer > div > img {
    position: absolute;
    left: 5px;
    top: 5px;
    height: 75%;
}

.shareBtn {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);

    display: flex;
    gap: 5px;

    background-color: lightgreen;
    border-radius: 5px;
    padding: 4px;

    font-size: .8rem;
    letter-spacing: -.05rem;

    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    transition: transform .5s .5s;

    transform-origin: top;

    pointer-events: all;

}

.shareBtn:hover {
    background-color: lightgray;
    cursor: pointer;
}

.shareBtn > img {
    align-self: center;

    height: 15px;
}
