.modalBackground {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100svh;

    background-color: rgba(0, 0, 0, 0.5);

    pointer-events: all;

    animation: backgroundAppear .3s;
}

@keyframes backgroundAppear {
    from {
        opacity: 0
    }
    to {
        opacity: 1;
    }
}

.mainContainer {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    width: 80vw;
    max-width: 400px;
    height: 200px;
    background-color: white;

    border-radius: 5px;

    box-shadow: var(--window-shadow);

    display: flex;
    flex-direction: column;
    align-items: center;

    overflow: hidden;

    transform-origin: left top;

    animation: mainContainerAppear .3s;

    pointer-events: all;

}

@keyframes mainContainerAppear {
    from {
        opacity: 0;
        scale: 0;
    }
    to {
        opacity: 1;
        scale: 1;
    }
}

.titleContainer {
    align-self: stretch;

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;

    background-color: lightgreen;

    outline: solid black 2px;

}

.title {
    letter-spacing: -.05rem;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.title > img {
    height: 20px;
}

.closeBtn {
    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    transition: .1s;
}

.closeBtn:hover {
    transform: scale(1.1);
}

.closeBtn > img {
    height: 20px;
}

.contentContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;

    flex-grow: 1;
    width: 80%;
}

.input {
    width: 100%;
}

.copyBtn {
    position: relative;
    font-size: .8rem;
    letter-spacing: -.02rem;

    border-radius: 2.5px;
    padding: 2.5px 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 1px;

    color: black;
    align-self: end;
}

.copiedNotification {
    position: absolute;

    left: 50%;
    bottom: calc(100% + 10px);
    transform: translateX(-50%);

    padding: 5px 10px;
    border-radius: 5px;

    background-color: black;
    color: lightgray;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    transition: opacity .3s;
}

.copiedNotification:after {
    position: absolute;
    content: '';

    left: 50%;
    bottom: -10px;
    transform: translateX(-50%);

    width: 20px;
    height: 10px;
    background-color: black;

    clip-path: polygon(50% 100%, 0 0, 100% 0);
}

.copyBtn:hover {
    background-color: lightgray;
    cursor: pointer;
}
