.toggleBtn {
    position: absolute;
    right: 1rem;
    top: 1rem;
    background-color: white;
    padding: 10px;

    cursor: pointer;
}

.toggleBtn:hover {
    background-color: gray;
}

.closeBtn {
    position: absolute;
    top: 5px;
    right: 5px
}

.container {
    position: absolute;
    width: 80vw;
    height: 80svh;

    background-color: rgba(255, 255, 255, 0.9);

    padding: 10px;
}
