.mainContainer {
    position: absolute;
    left: 10px;
    bottom: 10px;

    height: 40px;

    background-color: rgba(255, 255, 255, 1);
    box-shadow: var(--window-shadow);
    border-radius: 5px;

    overflow: hidden;

    pointer-events: all;

    padding: 5px 10px;

    display: flex;
    align-items: center;
    gap: 5px;

    cursor: pointer;

    animation: appear .5s;
}

.mainContainer:active, .mainContainer:hover {
    background-color: lightgray;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.mainContainer > img {
    height: 75%;
    aspect-ratio: 1/1;

    rotate: 180deg;
}

.mainContainer > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    text-align: center;

    font-size: .8rem;
    line-height: .65rem;
    letter-spacing: -.05rem;
}
