.container {
    z-index: 999;
    position: fixed;

    left: 50%;
    top: 20px;
    transform: translateX(-50%);

    width: 90vw;
    max-width: 800px;
    /*height: 90svh;*/
    max-height: 90svh;

    background-color: white;
    border-radius: 10px;

    display: flex;
    flex-direction: column;

    padding: 20px;

    pointer-events: all;

    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    text-align: justify;

    overflow: scroll;
}

.container > h3 {
    align-self: center;
    text-align: center;
}

.container > button {
    align-self: center;

    margin-top: 20px;

    width: 100px;
    height: 50px;
}
