.mainContainer {
    position: absolute;
    left: 10px;
    top: 70px;

    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 5px;

    aspect-ratio: 1/1;

    cursor: pointer;

    pointer-events: all;

    transition: transform .2s;
}

.mainContainer:active, .mainContainer:hover {
    transform: scale(1.1);
}

.mainContainer > img {
    height: 35px;
}

.notification {
    position: absolute;

    left: calc(100% + 10px);
    top: 50%;
    transform: translateY(-50%);

    width: min-content;

    padding: 5px 15px 5px 10px;

    background-color: white;
    border-radius: 5px;

    font-size: .8rem;
    letter-spacing: -.05rem;
    line-height: .8rem;

    transition: opacity .5s;
}

.notification:before {
    content: '';

    position: absolute;
    top: 50%;
    right: 2.5px;

    transform: translateY(-50%);

    height: 80%;
    width: 2px;

    background-color: red;
}

.notification:after {
    content: '';

    position: absolute;
    left: -10px;
    top: 50%;
    transform: translateY(-50%);

    width: 10px;
    aspect-ratio: 1/1;

    background-color: inherit;
    clip-path: polygon(100% 100%, 0 50%, 100% 0);
}
