.loadingScreen {
    z-index: 100;

    position: fixed;
    width: 100vw;
    height: 100svh;

    background-color: black;

    transition: opacity 1s;

    display: flex;
    justify-content: center;
    align-items: center;
}

.loadingScreen > img {
    max-width: 75%;
    animation: opacityBlinking 1s alternate infinite linear;
}

@keyframes opacityBlinking {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
