.container {
    position: fixed;
    left: 0;
    top: 0;

    width: 3rem;
    aspect-ratio: 1/1;

    background-color: pink;

    transform-origin: top left;
}

.container:before{
    content: "";
    position: absolute;
    border-radius: 50%;
    inset: 0;
    background: conic-gradient(black calc(60*1%),#0000 0);
}

