.mainContainer {
    position: fixed;

    width: 80vw;
    max-width: 400px;
    height: 100px;

    transform-origin: top left;

    border-radius: 5px;
    background-color: white;

    display: grid;
    grid-template-rows: 40% 60%;
    grid-template-columns: 100%;

    box-shadow: var(--window-shadow);

    pointer-events: all;
}

.infoContainer {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}

.infoTile {

    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;

    text-align: center;

    line-height: .9rem;
}

.infoTile > div:first-of-type {
    font-size: 1.5rem;
}

.infoTile > div:nth-of-type(2) {
    font-size: .7rem;
    letter-spacing: -.025rem;
}

.buttonsContainer {
    display: flex;
    justify-content: space-evenly;

    margin: 0 10px
}

.btn {
    position: relative;
    padding: 5px 10px;

    width: 30%;
    height: 50%;

    text-align: center;
    font-size: .8rem;
    letter-spacing: -.05rem;
    line-height: .75rem;

    align-self: center;
    justify-self: center;

    display: flex;
    justify-content: center;
    align-items: center;

    gap: 5px;

    border-radius: 2.5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 2px, rgb(51, 51, 51) 0px 0px 0px 2px, rgba(0, 0, 0, 0.24) 0px 3px 4px 5px;

    cursor: pointer;
}

.line {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2.5px;

    width: 2px;
    height: 85%;
}

.btn > img {
    height: 15px;
}

.btn:active, .btn:hover {
    background-color: lightgray;
}
